  text-overflow: ellipsis;
  margin-top: 0.2rem;
}
.product-details-right-img{
  line-height: 0.6rem;
  margin-left: 0.1rem; 
}
.product-details-right-img img{
  height: 0.3rem;
}

/*  */
.product-recommend-area{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  padding-bottom: 0.28rem;
}
.product-recommend-list{
  margin-top: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-right: 0.3rem;
}
.product-recommend-item{

}
.product-recommend-img{
  height: 1.78rem;
  width: 3.2rem;
}
.product-recommend-title{
  margin-top: 0.16rem;
  font-size: 0.26rem;
  line-height: 0.28rem;
  height: 0.84rem;
  color: #000;

}
.product-recommend-can{
  font-size: 0.22rem;
  color: #8f8f8f;
  margin-bottom: 0.12rem;
}
.product-recommend-num{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.product-recommend-cur{
  font-size: 0.22rem;
  color: #ff8800;
}
.product-recommend-price{
  font-size: 0.3rem;
  color: #ff8800;
}
.product-recommend-qi{
  font-size: 0.2rem;
  color: #8f8f8f;
}
.product-recommend-snum{
  line-height: 0.3rem;
  font-size: 0.2rem;
  color: #8f8f8f;
}

/*  */
.product-footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 101;
  height: 0.9rem;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  box-shadow:0 -0.05rem 0.1rem #ebebeb;
}
.product-collection-btn{
  width: 27%;
  text-align: center;

}
.product-collection-icon{
  display: flex;
  flex-direction: column;
  display: inline-block;
  margin-top: 0.2rem;
  color: #999;
  width: 0.5rem;
  font-size: 0.24rem;
}
.product-collection-icon img{
  height: 0.3rem;
  width: 0.4rem;
  margin-bottom: 0.1rem;
}
.product-footer-btn{
  width: 73%;
  text-align: center;
  line-height: 0.98rem;
  background-color: #16a3e6;
  color: #fff;
}

</style>



